.post {
    background-color: #fff;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 20px;
  }
  
  .post h2 {
    font-size: 2rem;
    margin-bottom: 10px;
  }
  
  .post img {
    width: 100%;
    height: 400px;
    object-fit: cover;
    margin-bottom: 10px;
  }
  
  .post p {
    font-size: 1.2rem;
    line-height: 1.5;
    margin-bottom: 10px;
  }
  
  .post button {
    margin-top: 10px;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    background-color: #eee;
    cursor: pointer;
  }
  
  .post button:hover {
    background-color: #333;
    color: #fff;
  }
  