.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

.title {
  font-size: 2rem;
  margin-bottom: 20px;
  text-align: center;
}

.posts {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.pagination button {
  margin: 0 5px;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  background-color: #eee;
  cursor: pointer;
}

.pagination button.active {
  background-color: #333;
  color: #fff;
}

.post-card {
  background-color: #fff;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  padding: 20px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
}

.post-card h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.post-card p {
  flex: 1;
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.post-card button {
  margin-top: 10px;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  background-color: #eee;
  cursor: pointer;
}

.post-card button:hover {
  background-color: #333;
  color: #fff;
}